<template>
  <div class="login">
    <!-- 头部 NavBar-->
    <van-nav-bar
      title="会员登陆"
      left-arrow
      @click-left="$router.go(-1)"
    />
    <!-- 主体 -->
    <div class="container">
      <div class="title">
        <h3>手机号登录</h3>
        <p>未注册的手机号登录后将自动注册</p>
      </div>

      <div class="form">
        <div class="form-item">
          <input v-model="mobile" class="inp" maxlength="11" placeholder="请输入手机号码" type="text">
        </div>
        <div class="form-item">
          <input v-model="picCode" class="inp" maxlength="5" placeholder="请输入图形验证码" type="text">
          <img @click="getPicCode" v-if="picUrl" :src="picUrl" alt="">
        </div>
        <div class="form-item">
          <input v-model="msgCode" class="inp" placeholder="请输入短信验证码" type="text">
          <button @click="getCode">{{ second===totalSecond?'获取验证码':second+'秒后重新发送' }}</button>
        </div>
      </div>

      <div @click="login" class="login-btn">登录</div>
    </div>
  </div>
</template>

<script>
import { getPicCode, getMsgCode, codeLogin } from '@/api/login.js'

export default {
  name: 'LoginIndex',
  data () {
    return {
      picKey: '', // 请求传递的图形验证码对应的唯一标识。
      picUrl: '', // 用于渲染图片的地址
      totalSecond: 60, // 倒计时总秒数
      second: 60, // 倒计时当前秒数，开启定时器后 second--
      timer: null, // 存定时器 id
      mobile: '', // 手机号
      picCode: '', // 用户输入的图形验证码。
      msgCode: '' // 短信验证码
    }
  },
  methods: {
    // 获取图形验证码
    async getPicCode () {
      const { data: { base64, key } } = await getPicCode()
      this.picUrl = base64 // 存储地址
      this.picKey = key // 存储唯一标识
      // this.$toast.success('获取图形验证码成功')
    },
    // 校验 手机号 和 图形验证码 是否合法
    // 通过校验 返回 true，不通过 返回 false
    validFn () {
      if (!/^1[3-9]\d{9}$/.test(this.mobile)) {
        this.$toast.fail('请输入正确的手机号')
        return false
      }
      if (!/^\w{4}$/.test(this.picCode)) {
        this.$toast.fail('请输入正确的图形验证码')
        return false
      }
      return true
    },
    // 获取短信验证码
    async getCode () {
      if (!this.validFn()) {
        // 如果没通过校验，没必要往下
        return
      }
      // 当目前没有定时器开着，且totalSecond 和 second 一致，才可以倒计时
      if (!this.timer && this.second === this.totalSecond) {
        // 发送请求（预期：希望响应的status状态码非200，最好抛出一个错误，await只会等待成功的promise）
        await getMsgCode(this.picCode, this.picKey, this.mobile)
        // 模拟获取验证码，这里默认246810
        this.$toast.success('验证码发送成功')
        // 开启倒计时
        this.timer = setInterval(() => {
          // console.log(`正在倒计时：${this.second}`)
          this.second--
          if (this.second <= 0) {
            clearInterval(this.timer)
            this.timer = null // 重置定时器 id
            this.second = this.totalSecond // 重置当前秒数
          }
        }, 1000)
      }
    },
    async login () {
      // 校验 手机号 和 图形验证码 是否合法
      if (!this.validFn()) {
        return
      }
      // 校验短信验证码
      if (!/^\d{6}$/.test(this.msgCode)) {
        this.$toast.fail('请输入正确的手机验证码')
        return
      }
      console.log('发送登录请求')

      const res = await codeLogin(this.mobile, this.msgCode)
      this.$store.commit('user/setUserInfo', res.data)
      console.log(res)
      // 提示
      this.$toast('登陆成功')
      // 1.进行判断，看地址栏有没有回弹参数，
      // 2.有就说明是其他页面，拦截到登录页的，需要回跳
      const url = this.$route.query.backUrl || '/'
      this.$router.replace(url)
    }
  },
  created () {
    this.getPicCode()
  },
  // 销毁组件时离开页面清除定时器
  destroyed () {
    clearInterval(this.timer)
  }
}
</script>

<style lang="less" scoped>
.container {
  padding: 49px 29px;

  .title {
    margin-bottom: 20px;
    h3 {
      font-size: 26px;
      font-weight: normal;
    }
    p {
      line-height: 40px;
      font-size: 14px;
      color: #b8b8b8;
    }
  }

  .form-item {
    border-bottom: 1px solid #f3f1f2;
    padding: 8px;
    margin-bottom: 14px;
    display: flex;
    align-items: center;
    .inp {
      display: block;
      border: none;
      outline: none;
      height: 32px;
      font-size: 14px;
      flex: 1;
    }
    img {
      width: 94px;
      height: 31px;
    }
    button {
      height: 31px;
      border: none;
      font-size: 13px;
      color: #cea26a;
      background-color: transparent;
      padding-right: 9px;
    }
  }

  .login-btn {
    width: 100%;
    height: 42px;
    margin-top: 39px;
    background: linear-gradient(90deg,#ecb53c,#ff9211);
    color: #fff;
    border-radius: 39px;
    box-shadow: 0 10px 20px 0 rgba(0,0,0,.1);
    letter-spacing: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
</style>
